/* ----- Header ------------------------ */
.header {
  position: relative;
  box-shadow: 0 0 2px rgba(111, 28, 87, 0.6);
  background-color: $cl-site-second;
  border-bottom: 1px solid rgba(126, 32, 86, 0.5);
  box-shadow: 0 0 2px rgba(111, 28, 87, 0.6);
  &__inner {
    padding: 10px 0;
    position: relative; } }

.logo {
  a {
    display: block;
    outline: none; }

  &_header {
    max-width: 140px;
    margin-right: 10px;
    flex-shrink: 0; } }

.header-menu-desc, .header-info__btn {
  display: none; }

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header-info {
  margin-left: auto;
  margin-right: 50px;
  &__tel {
    color: #fdecd4;
    font-size: 10px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0.35px;
    &-link {
      color: #fdecd4;
      display: inline-block;
      text-decoration: none;
      border-bottom: 1px solid transparent;
      border-top: 1px solid transparent;
      transition-duration: 0.3s;
      &:hover {
        border-bottom-color: #fdecd4;
        border-top-color: #fdecd4;
        text-decoration: none; } } } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .menu-item_hidden_tel {
    display: none; }
  .header-info__btn {
    display: block;
    margin-left: 20px; }
  .header-info {
    display: flex;
    align-items: center;
    &__tel {
      font-size: 12px; } }
  /* end media min width 480 pixels */ }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .header-info {
    margin-right: 60px;
    &__tel {
      font-size: 14px;
      line-height: 20px;
      &-link {
        padding: 5px; } } }

  .logo_header {
    max-width: 250px; }

  /* end media min width 640 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .header__inner {
    padding: 14px 0; }
  .header-menu-desc {
    display: block;
    margin-left: auto;
    .menu {
      display: flex;
      align-items: center;
      text-align: center; }
    .menu-item {
      display: inline-block;
      margin: 0 10px;
      width: auto;
      font-size: 14px;
      line-height: 20px; } }
  .menu-item {
    color: #fdecd4;
    letter-spacing: 0.35px;
    flex-shrink: 0;
    &__link {
      display: inline-block;
      padding: 5px;
      text-decoration: none;
      border-bottom: 1px solid transparent;
      border-top: 1px solid transparent;
      transition-duration: 0.3s;
      &:hover {
        border-bottom-color: #fdecd4;
        border-top-color: #fdecd4;
        text-decoration: none; } } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .header-info__btn {
    margin-left: 28px; }

  .header-menu-desc {
    margin-right: auto; }

  .header-menu-desc .menu {
    margin-left: 20px; }

  .header-menu-desc .menu-item {
    margin: 0 16px; }
  /* end media min width 1200 pixels */ }

/* ----- END Header -------------------- */
