/* DOTS */
$SL-dot-wh: 12px;
$SL-dot-margin: 6;
$wh-dots: $SL-dot-wh+($SL-dot-margin*2);

.slick-dots {
  position: absolute;
  width: 100%;
  text-align: center;
  bottom: -30px;

  li {
    padding: 0;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    margin: 0 $SL-dot-margin+(px);

    button {
      font-size: 0;
      line-height: 0;
      padding: 0;
      margin: 0;
      border: none;
      box-shadow: none;
      outline: none;
      display: inline-block;
      position: relative;

      background-color: #5c5c5c;
      width: 30px;
      height: 3px;


      &:hover:before, &:focus:before {
        opacity: 1; }

      &:before {
        content: ' ';
        background-color: transparent;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -1px;
        margin-left: -(60/2)+(px);
        background-color: transparent;
        width: 60px;
        height: 4px; } }

    &.slick-active button {
      background-color: #260c3d;
      &:before {
        background-color: #260c3d; } } } }

.slider-dots {
  position: relative;
  z-index: 1;
  .slick-dots {
    position: static;
    width: 100%;
    text-align: center;
    bottom: auto; } }

.slider-points__dots {
  margin-bottom: 20px;
  margin-top: 5px; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .slick-dots {
    li {
      margin: 0 20+(px);

      &.slick-active {
        margin: 0 35px; } } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .slider-points__dots {
    margin-top: 15px; }
  /* end media min width 1000 pixels */ }
