/* ----- Footer ------------------------ */
.footer {
  background-color: $cl-site-second2;
  color: $cl-light;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.35px;
  .title-site, h1, h2, h3, h4, h5, h6 {
    color: $cl-light; }
  &__dark {
    padding: 20px 0 10px;
    box-shadow: 0 0 2px rgba(111, 28, 87, 0.6);
    background-color: $cl-site-three; }

  &__inner {
    padding: 40px 0 10px; } }

.f-grid__item {
  margin-bottom: 30px; }

.f-menu {
  .menu li {
    color: $cl-light;
    font-family: $site-font-family;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.35px;
    margin: 0 0 20px;
    a {
      color: $cl-light;
      border: none;
      display: inline;
      &:hover {
        text-decoration: underline; } } } }

.logo_footer {
  margin-bottom: 20px; }

.social-list {
  margin-left: -5px;
  li {
    display: inline-block;
    vertical-align: middle;
    margin-right: 5px;
    margin-bottom: 10px;
    &:last-child {
      margin-right: 0; }
    a {
      outline: none;
      transition-duration: 0.3s;
      display: block;
      padding: 5px;
      &:hover, &:focus {
        opacity: 0.8;
        transform: scale(0.85); } } } }

.bl-txt-icon {
  margin-bottom: 10px;
  &__item {
    color: $cl-light;
    font-size: 14px;
    line-height: 22px;
    padding-left: 38px;
    letter-spacing: 0.4px;
    position: relative;
    z-index: 1;
    display: inline-block;
    text-decoration: none;
    .icon {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0; } } }
a.bl-txt-icon__item {
  &:hover {
    text-decoration: underline; } }

.f-tt {
  color: $cl-site-7;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.2;
  letter-spacing: 0.45px;
  margin-bottom: 10px; }

.f-sub-menu {
  text-align: center;
  li {
    color: #cbcbcb;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.35px;
    margin-bottom: 10px;
    a {
      color: #cbcbcb;
      text-decoration: none;
      &:hover {
        text-decoration: underline; } } } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .f-grid {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    &__item {
      max-width: 50%;
      min-width: 50%;
      padding: 0 10px; } }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .footer {
    font-size: 14px;
    line-height: 24px; }
  .bl-txt-icon {
    &__item {
      font-size: 16px; } }

  .f-sub-menu {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .f-grid {
    &__item {
      max-width: 25%;
      min-width: 25%;
      padding: 0 10px; } }

  .f-sub-menu {
    li {
      margin-right: 80px;
      &:first-child {
        margin-right: auto; }
      &:last-child {
        margin-right: 0; } } }

  .footer__inner {
    padding-top: 70px;
    padding-bottom: 1px; }

  .logo_footer {
    margin-bottom: 32px; }

  .f-txt {
    padding-top: 12px;
    margin-bottom: 43px; }

  .social-list li {
    margin-right: 25px; }

  .f-grid__item {
    &:first-child {
      min-width: 31%;
      max-width: 31%; }
    &:nth-child(2) {
      min-width: 21%;
      max-width: 21%; }
    &:nth-child(3) {
      min-width: 24%;
      max-width: 24%;
      padding-top: 10px; }
    &:nth-child(4) {
      min-width: 24%;
      max-width: 24%; } }

  .f-menu .menu li {
    font-size: 14px;
    padding: 10px 0;
    margin: 0; }

  .bl-txt-icon {
    margin-bottom: 15px; }
  /* end media min width 1000 pixels */ }

/* ----- END Footer -------------------- */
