/* ----- Controls styles ----------------- */
.btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: $btn-padding;
  font: $btn-font;
  border: none;
  border-radius: $btn-radius;
  text-align: center;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  color: $btn-color;
  background-color: $btn-fill-color;
  letter-spacing: 0.45px;

  &.btn_disable {
    cursor: default;
    &:hover {
      color: $btn-color;
      background-color: $btn-fill-color; } }

  &:hover {
    text-decoration: none;
    color: $btn-color-hover;
    background-color: $btn-fill-hover-color; }

  &_second-st {
    background-color: $btn-fill-second-color;
    background-image: $grad2;
    &.btn_disable {
      &:hover {
        background-color: $btn-fill-second-color;
        background-image: $grad2; } }

    &:hover {
      background-color: $btn-fill-second-hover-color;
      background-image: $grad2-hover; } }

  &_three-st {
    background-color: $btn-fill-three-color;
    background-image: $grad3;
    &.btn_disable {
      &:hover {
        background-color: $btn-fill-three-color;
        background-image: $grad3; } }

    &:hover {
      background-color: $btn-fill-three-hover-color;
      background-image: $grad3-hover; } }

  &_four-st {
    background-color: $btn-fill-four-color;
    background-image: $grad4;
    &.btn_disable {
      &:hover {
        background-color: $btn-fill-four-color;
        background-image: $grad4; } }

    &:hover {
      background-color: $btn-fill-four-hover-color;
      background-image: $grad4-hover; } }

  &_light-st {
    background-color: #fdecd4;
    color: #262626;
    &.btn_disable {
      &:hover {
        background-color: #fdecd4;
        color: #262626; } }

    &:hover {
      background-color: lighten(#fdecd4, 10%);
      color: #000; } }

  &_border {
    border: $btn-border;
    color: $btn-border-color-text;
    line-height: $btn-border-line-height;
    background: transparent;

    &:hover {
      color: $btn-border-hover-color-text;
      background-color: $btn-border-hover-color; }

    &.btn_second-st {
      border-color: $btn-fill-second-color;
      color: $btn-fill-second-color;

      &:hover {
        color: $btn-border-hover-color-text;
        background-color: $btn-fill-second-color;
        border-top: none;
        border-bottom: none;
        line-height: 24px; } }

    &.btn_three-st {
      border-color: $btn-fill-three-color;
      color: $btn-fill-three-color;

      &:hover {
        color: $btn-border-hover-color-text;
        background-color: $btn-fill-three-color;
        border-top: none;
        border-bottom: none;
        line-height: 24px; } }

    &.btn_four-st {
      border-color: $btn-fill-four-color;
      color: $btn-fill-four-color;

      &:hover {
        color: $btn-border-hover-color-text;
        background-color: $btn-fill-four-color;
        border-top: none;
        border-bottom: none;
        line-height: 24px; } }

    &.btn_light-st {
      border-color: #fdecd4;
      color: #fdecd4;
      background: none;

      &:hover {
        background-color: #fdecd4;
        color: #262626; } } }

  &_min-width {
    min-width: $btn-width-min; }

  &_min-width_long {
    min-width: 290px; }

  &_max {
    width: 100%; }

  &_mini {
    padding: $btn-padding-mini;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.35px;
    border-radius: 15px;

    &.btn_min-width {
      min-width: $btn-width-min-mini; } }

  &_sm {
    padding: $btn-padding-sm;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.35px;
    border-radius: 17px;

    &.btn_min-width {
      min-width: $btn-width-min-sm; } }

  &_md {
    padding: $btn-padding-md;
    border-radius: 35px;
    font-weight: 600;
    letter-spacing: 0.7px;

    &.btn_min-width {
      min-width: $btn-width-min-md; } }

  &_lg {
    padding: $btn-padding-lg;
    border-radius: 40px;
    font-weight: 700;
    letter-spacing: 1px;

    &.btn_min-width {
      min-width: 290px; } }

  &__icon-text {
    display: inline-block;
    vertical-align: middle; }

  &__icon {
    display: inline-block;
    vertical-align: middle;
    font-style: normal;
    line-height: 0px;

    &_right {
      margin-left: 5px; }

    &_left {
      margin-right: 5px; } }

  // &_font-bold
  //   font-family: $btn-font-family-bold
  //   font-weight: $btn-font-weight-bold

  // &_font-light
  //   font-family: $btn-font-family-light
  //   font-weight: $btn-font-weight-light

  // &_size-sm
  //   font-size: $btn-size-sm
  // &_size-md
  //   font-size: $btn-size-md
  // &_size-lg
  //   font-size: $btn-size-lg

  // &_up
  //   text-transform: uppercase

  // &_radius-sm
 }  //   border-radius: $btn-radius-sm

input.btn {
  padding-top: 0;
  padding-bottom: 0;
  height: $input-height;
  width: auto;
  &_max {
    width: 100%; } }

.form-group-grid__four-col {
  .btn {
    vertical-align: bottom;
    display: block; } }

.placeholder {
  color: $st-color-placeholder; }

input,
textarea {
  @include placeholder-color($st-color-placeholder); }

::-webkit-contacts-auto-fill-button {
  opacity: 0; }

input[type="password"]::-webkit-contacts-auto-fill-button {
  opacity: 1; }

input,
textarea {
  position: relative;
  height: $input-height;
  width: 100%;
  padding: $input-padding;
  border: $input-border;
  font: $input-font;
  color: $input-color;
  background-color: $input-fill-color;
  outline: none;
  border-radius: $input-radius;
  -webkit-appearance: none;
  box-shadow: $input-shadow;

  &:hover {
    border-color: $input-hover-border-color; }

  &:focus {
    border-color: $input-focus-border-color; } }

input[type="radio"], input[type="checkbox"] {
  width: auto;
  margin: 3px 5px 3px 0;
  height: 12px; }

input[type="checkbox"] {
  -webkit-appearance: checkbox; }
input[type="radio"] {
  -webkit-appearance: radio; }

textarea {
  vertical-align: top;
  height: $textarea-height;
  padding: $textarea-padding;
  resize: none; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .btn {
    &_min-width_long {
      min-width: 355px; } }

  input {
    height: 64px;
    font-size: 16px;
    padding: 0 30px; }

  textarea {
    height: 175px;
    padding: 15px 30px; }
  /* end media min width 768 pixels */ }

/* ----- END controls ------------------- */
