/* ----- Slider personal ------------------- */

.p-box {
  box-shadow: 0 0 10px rgba(14, 17, 25, 0.2);
  border-radius: 5px;
  background-color: $cl-light; }

.slider-review {
  .slider-slide {
    padding: 15px;
    max-width: 300px; } }

.p-box__img {
  width: 160px;
  height: 200px;
  margin: 0 auto;
  >img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center top; } }

.p-box-info {
  padding: 20px 20px 15px;
  > *+* {
    margin-top: 5px; }
  &__name {
    color: #262626;
    font-size: 16px;
    font-weight: 600;
    line-height: 1.2;
    letter-spacing: 0.5px; }
  &__position {
    color: #2e2e2e;
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
    letter-spacing: 0.4px; }
  p {
    color: #2e2e2e;
    letter-spacing: 0.35px;
    margin-bottom: 5px; } }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .slider-review {
    .slider-slide {
      max-width: 600px; } }
  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .p-box-info {
    &__name {
      font-size: 20px; }
    &__position {
      font-size: 16px;
      line-height: 22px; }
    p {
      line-height: 22px; } }
  .slider-review {
    .slider-slide {
      max-width: 760px; } }

  .p-box {
    display: flex;
    justify-content: flex-start;
    overflow: hidden;
    &__img {
      min-width: 183px;
      min-height: 220px;
      height: auto;
      max-width: 183px; } }

  .p-box-info__position {
    margin-bottom: 15px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .slider-review {
    .slider-slide {
      max-width: 970px;
      padding: 50px 15px; } }

  .p-box-info {
    padding: 30px 30px 20px; }
  /* end media min width 1000 pixels */ }

/* ----- END Slider personal --------------- */
