/* ----- Card component ------------------- */

.card {
  box-shadow: 0 0 20px rgba(14, 17, 25, 0.2);
  border-radius: 10px;
  background-color: $cl-site-second2;
  text-decoration: none;
  outline: none;
  display: block;
  padding: 20px 15px;
  margin-bottom: 20px;
  color: #fff;
  &-info {
    > * + * {
      margin-top: 10px; }
    > *:last-child {
      margin-bottom: 0; } }
  &-cap {
    min-height: 100px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    &-pic {
      flex-shrink: 1;
      &__icon {
        margin-bottom: 5px;
        min-height: 88px; }
      &__name {
        color: $cl-light;
        font-size: 16px;
        font-weight: 600;
        line-height: 20px;
        text-transform: uppercase;
        letter-spacing: 0.5px; } }
    &-icon {
      flex-shrink: 0; } } }

a.card {
  transition: all 0.2s linear;
  &:hover, &:focus {
    box-shadow: 3px 3px 5px rgba(14, 17, 25, 0.7); } }

.spec-box {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  align-items: center;
  &__item {
    min-width: 50%;
    max-width: 50%; }
  &__tt {
    margin-bottom: 20px;
    width: 100%;
    min-width: 100%;
    max-width: 100%;
    .title-site_h2 {
      margin-bottom: 5px; } } }

.tt-name {
  color: $cl-light-2;
  font-size: 32px;
  line-height: 1;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.8px; }

.card-price {
  box-shadow: 0 0 20px rgba(14, 17, 25, 0.2);
  border-radius: 5px;
  background-color: #554165;
  margin-bottom: 30px;
  padding: 20px 15px;
  color: $cl-light;
  font-weight: 600;
  &__item {
    padding: 10px 0; }
  &__name {
    color: $cl-light;
    font-size: 16px;
    line-height: 1.2;
    letter-spacing: 0.6px;
    margin-bottom: 5px; }

  &__value {
    background-image: linear-gradient(60deg, $cl-site-7 0%, #f76894 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: #262626;
    font-size: 22px;
    font-weight: 700;
    line-height: 1.2;
    text-transform: uppercase;
    letter-spacing: 0.9px;
    span {
      background: none;
      -webkit-background-clip: border-box;
      background-clip: border-box;
      -webkit-text-fill-color: #bdbdbd;
      font-weight: 500;
      text-decoration: line-through;
      margin-left: 20px; } } }

.card-cap-pic__value {
  background-image: linear-gradient(60deg, $cl-site-7 0%, #f76894 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: $cl-light;
  font-size: 80px;
  font-weight: 800;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 3px; }

.card-cap-icon_set {
  flex-shrink: 2;
  position: relative;
  &:after {
    content: ' ';
    position: absolute;
    top: -32px;
    right: -27px;
    border: 10px solid #554165;
    bottom: 0;
    left: 15px; } }

/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .card-list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-left: -5px;
    margin-right: -5px;
    &__item {
      min-width: 50%;
      max-width: 50%;
      padding-right: 5px;
      padding-left: 5px;
      margin-bottom: 10px;
      .card {
        height: 100%;
        margin-bottom: 0; } } }
  .card-cap {
    min-height: 150px; }
  /* end media min width 640 pixels */ }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .card {
    &-cap {
      &-pic {
        &__name {
          font-size: 20px;
          line-height: 28px; } } } }

  .tt-name {
    color: $cl-light-2;
    font-size: 60px; }

  .spec-box__item {
    min-width: 100px;
    max-width: none; }

  .spec-box__tt {
    order: 2;
    min-width: 70%;
    max-width: 70%; }
  .spec-box__item_order {
    order: 3; }

  .spec-box__item_top, .spec-box__item_bottom {
    min-width: 50%;
    max-width: 50%; }
  .spec-box__item_middle_first {
    text-align: left;
    min-width: 15%;
    max-width: 15%; }
  .spec-box__item_middle_two {
    text-align: right;
    min-width: 15%;
    max-width: 15%; }

  .spec-box {
    background: url(../img/ellips1.png) center top 123px no-repeat;
    background-size: 90% auto; }

  .card-price {
    &__value {
      font-size: 28px; }
    &__name {
      font-size: 20px; } }

  .brand-bl {
    .tt-name {
      font-size: 50px; } }

  .card-cap-pic__value {
    font-size: 120px;
    margin-top: -10px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .spec-box {
    background-position: center top 100px; }

  .card-price {
    padding: 30px 25px;
    &__value {
      font-size: 36px; }
    &__name {
      font-size: 24px; } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .card {
    padding: 25px; }
  .card-list {
    margin-left: -15px;
    margin-right: -15px;
    &__item {
      min-width: 33.3%;
      max-width: 33.3%;
      padding-left: 15px;
      padding-right: 15px;
      margin-bottom: 30px;

      &_half {
        min-width: 50%;
        max-width: 50%;
        padding-left: 30px;
        padding-right: 30px;
        margin-bottom: 60px; } } }
  .spec-box {
    background-position: center top 60px; }

  .tt-name {
    font-size: 72px; }

  .brand-bl {
    .tt-name {
      font-size: 72px; } }

  .card-cap-icon_set {
    flex-shrink: 2;
    position: relative;
    &:after {
      border-width: 20px;
      top: -62px;
      right: -77px; } }

  .card_lg {
    padding: 30px 45px;
    .card-cap {
      min-height: 167px; } }

  .card-list_max-width {
    margin-left: 0;
    margin-right: 0; }
  /* end media min width 1200 pixels */ }

/* ----- END Card component --------------- */
