/* ----- Inner pages ------------------- */
.ac-cap {
  box-shadow: 0 0 20px rgba(14, 17, 25, 0.2);
  border-radius: 10px;
  background-color: $cl-site-second2;
  color: #ffdea8;
  font-size: 18px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0.6px;
  padding: 20px 40px 20px 15px;
  display: block;
  text-decoration: none;
  outline: none;
  position: relative;
  &:after {
    content: ' ';
    position: absolute;
    top: 50%;
    right: 10px;
    background-image: url(../img/sprite.png);
    background-repeat: no-repeat;
    width: 17px;
    height: 25px;
    margin-top: -12px;
    background-position: -506px -5px; } }

.ac-body {
  padding: 15px 0;
  color: #2e2e2e;
  p {
    font-weight: 500;
    letter-spacing: 0.4px; } }

.accordion-list__item {
  margin-bottom: 10px; }

.list-dots {
  margin-bottom: 25px;
  li {
    position: relative;
    padding-left: 20px;
    margin-bottom: 15px;
    &:before {
      content: ' ';
      position: absolute;
      top: 5px;
      left: 0;
      width: 8px;
      height: 8px;
      background-color: $cl-dark;
      border-radius: 50%;
      background-image: linear-gradient(60deg, $cl-site-7 0%, #f76894 100%); } } }

.timebox {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  flex-wrap: wrap;
  border-bottom: 1px solid #4a3a57;
  padding-bottom: 10px;
  margin-bottom: 10px;
  > * {
    &:last-child {
      margin-right: 0; } }
  &__txt {
    color: #2e2e2e;
    line-height: 22px;
    letter-spacing: 0.35px;
    margin-right: 20px; }
  &__icon {
    margin-right: 10px; } }

.tt-md {
  color: #2e2e2e;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 0.4px;
  margin-bottom: 10px; }

.list-dots-sm {
  color: #2e2e2e;
  letter-spacing: 0.35px;
  > li {
    position: relative;
    padding-left: 10px;
    margin-bottom: 15px;
    &:before {
      content: ' ';
      position: absolute;
      top: 8px;
      left: 0;
      width: 3px;
      height: 3px;
      background-color: $cl-dark;
      border-radius: 50%; } } }

.ac-body {
  display: none; }

.accordion-list__item {
  &.current {
    .ac-cap {
      &:after {
        transform: rotate(180deg); } } } }

.skills-technologies {
  color: #fee4cc;
  font-size: 30px;
  font-weight: 700;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 1.35px;
  text-align: center;
  margin-bottom: 30px;
  li {
    margin-bottom: 10px; } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .ac-cap {
    font-size: 24px;
    line-height: 26px;
    padding: 30px 40px 30px 30px; }

  .ac-body {
    padding: 30px 5px 20px;
    p {
      font-size: 16px;
      line-height: 24px; }

    .list-dots {
      padding-left: 10px; } }

  .list-dots-sm {
    line-height: 22px;
    > li {
      &:before {
        top: 10px; } } }

  .list-dots li:before {
    top: 6px; }

  .timebox {
    margin-bottom: 20px; }
  .timebox__icon {
    margin-right: 20px; }

  .timebox__txt {
    margin-right: 40px; }

  .skills-technologies {
    font-size: 32px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 30px;
    li {
      position: relative;
      padding: 0 30px;
      &:first-child {
        &:before {
          display: none; } }
      &:before {
        content: ' ';
        position: absolute;
        top: 50%;
        left: -8px;
        width: 16px;
        height: 16px;
        margin-top: -8px;
        border-radius: 50%;
        background-color: $cl-dark;
        background-image: linear-gradient(60deg, $cl-site-7 0%, #f76894 100%); } } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .ac-body {
    padding: 30px 25px 20px; }
  .ac-cap {
    padding: 30px 70px 30px 60px;
    &:after {
      right: 50px; } }
  .ac-body {
    .list-dots {
      padding-left: 40px; } }

  .skills-technologies {
    font-size: 54px;
    margin-bottom: 0; }
  /* end media min width 1000 pixels */ }

/* ----- END Inner pages --------------- */
