// colors
$cl-site: #fc8a00;
$cl-site-second: #240a3a;
$cl-site-second2: #240a3b;
$cl-site-three: #1b062d;
$cl-site-four: #391557;
$cl-site-five: #fcd69f;
$cl-site-6: #311a47;
$cl-site-7: #fcd69f;
$cl-site-8: #94784a;

$cl-light: #fff;
$cl-light-2: #f3f3f3;
$cl-dark: #000;
$cl-emperor: #555;
$cl-mineShaft: #232323;
$cl-green: #00BD12;
$cl-porsche: #eab15d;
$cl-blue: #1f30ff;
$cl-scarlet: #ff2c1a;
$cl-canary: #fdfd58;
$cl-silver: #ccc;
$cl-alto: #ddd;
$cl-gallery: #eee;
$cl-silverChalice: #aaa;
$cl-tacao: #efae7c;

$grad1: linear-gradient(60deg, rgba(243, 31, 70, 0.8) 0%, rgba(243, 31, 70, 0.8) 100%);
$grad1-hover: linear-gradient(60deg, rgba(243, 31, 70, 1) 0%, rgba(243, 31, 70, 1) 100%);
$grad2: linear-gradient(to top, #240a3c 0%, #eb5081 100%);
$grad2-hover: linear-gradient(to top, lighten(#240a3c, 10%) 0%, lighten(#eb5081, 10%) 100%);
$grad3: linear-gradient(to top, #34095b 0%, #9071f5 100%);
$grad3-hover: linear-gradient(to top, lighten(#34095b, 10%) 0%, lighten(#9071f5, 10%) 100%);
$grad4: linear-gradient(to top, #c33029 0%, #fd825d 100%);
$grad4-hover: linear-gradient(to top, lighten(#c33029, 10%) 0%, lighten(#fd825d, 10%) 100%);

//scarlet | red || Canary | yellow

// Typography var
$site-font-style: normal;   //normal | italic | oblique | inherit
$site-font-variant: normal; //normal | small-caps | inherit
$site-font-weight: normal;
$site-fill-color: #fff;

$site-font-family: 'Montserrat', Arial, sans-serif;
$site-font-family-2: 'Roboto', Arial, sans-serif;
$site-font-family-3: 'Roboto', Arial, sans-serif;
$site-font-family-4: 'Roboto', Arial, sans-serif;
$site-font-family-5: 'Roboto', Arial, sans-serif;

$body-text-size: 12+(px)+/18+(px);
$site-text-size: 12+(px);
$site-line-height: 20+(px);

// $font-base: 'Roboto'
// $font-second: 'Roboto'

$site-text-color: #2e2e2e;

// Title site variables
$site-title-color: #262626;
$title-font-weight: $site-font-family;
$title-font-family: 600;
$title-line-height: 1.3+(em);

$site-h1-size-mobile: 30+(px);
$site-h2-size-mobile: 26+(px);
$site-h3-size-mobile: 24+(px);
$site-h4-size-mobile: 20+(px);
$site-h5-size-mobile: 18+(px);
$site-h6-size-mobile: 16+(px);

$site-h1-size: 36+(px);
$site-h2-size: 30+(px);
$site-h3-size: 26+(px);
$site-h4-size: 22+(px);
$site-h5-size: 20+(px);
$site-h6-size: 18+(px);


// Buttons variables
$btn-fill-color: #ce1d49;
$btn-fill-hover-color: lighten($btn-fill-color, 5%);
$btn-fill-second-color: #943263;
$btn-fill-second-hover-color: lighten($btn-fill-second-color, 5%);
$btn-fill-three-color: #7e5dd7;
$btn-fill-three-hover-color: lighten($btn-fill-three-color, 5%);
$btn-fill-four-color: #e15a44;
$btn-fill-four-hover-color: lighten($btn-fill-four-color, 5%);
$btn-color: #fff;
$btn-color-hover: #fff;

$btn-radius: 26+(px);
$btn-font: normal 500 18+px+/+24+px $site-font-family;
$btn-padding: ((52-24)/2)+px 35px;
$btn-width-min: 235px;

//border btn
$btn-border: 1px solid $btn-fill-color;
$btn-border-hover-color: $btn-fill-color;
$btn-border-line-height: 22px;
$btn-border-color-text: $btn-fill-color;
$btn-border-hover-color-text: $btn-color;

//mini
$btn-padding-mini: ((30-24)/2)+px 20px;
$btn-width-min-mini: 110px;
//sm
$btn-padding-sm: ((34-24)/2)+px 30px;
$btn-width-min-sm: 150px;
//md
$btn-padding-md: ((68-24)/2)+px 35px;
$btn-width-min-md: 265px;
//lg
$btn-padding-lg: ((74-24)/2)+px 40px;
$btn-width-min-lg: 290px;
//font-radius-sm
$btn-size-sm: 12px;
//font-radius-md
$btn-size-md: 16px;
//font-radius-lg
$btn-size-lg: 18px;
//font-family-bold
$btn-font-family-bold: $site-font-family;
$btn-font-weight-bold: 700;
//font-family-light
$btn-font-family-light: $site-font-family;
$btn-font-weight-light: 300;
//font-radius-sm
$btn-radius-sm: 5px;

// links variables
$color-link: #4a93df;
$color-h-link: inherit;

// color .placeholder
$st-color-placeholder: $cl-site-second2;

// Field Forms variables Input and Textarea
$input-height: 52+(px);
$input-padding: 0 15+(px);
$input-border: 2px solid #777;
$input-font: 14px $site-font-family;
$input-color: #222;
$input-fill-color: #fff;
$input-radius: 5px;
$input-shadow: none;

$input-hover-border-color: lighten($cl-site-second2, 25%);
$input-focus-border-color: $cl-site-second2;

// Navigation
$burger-fill: $cl-site-7;
$burger-fill-open: $cl-site-7;

$font-menu-item-mobile: normal normal 20+(px)+/24+(px) $site-font-family;
$font-menu-item: normal normal 16+(px)+/20+(px) $site-font-family;
$fill-menu: $cl-site-second;
$fill-menu-hover: darken($cl-site, 5%);

// Field Forms textarea
$textarea-height: 130+(px);
$textarea-padding: 10+(px) 15+(px);

// breakpoint
$mob: 375px;
$mob-md: 480px;
$mob-lg: 640px;
$tabl: 768px;
$lap: 1000px;
$desk: 1200px;
$desk-md: 1600px;
$desk-lg: 1900px;

