/* ----- Banner cap ------------------- */
.page-cap {
  background-color: $cl-site-second;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover; }

.banner-cap {
  color: $cl-light;
  padding: 40px 0;
  .title-site, h1, h2, h3, h4, h5, h6 {
    color: $cl-light; }

  &__txt {
    color: $cl-light;
    font-size: 16px;
    line-height: 1.2;
    letter-spacing: 0.5px;
    margin-bottom: 20px; }

  &__btn {
    margin-bottom: 30px; } }

.banner-cap-btns__tt {
  color: $cl-light;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.4px;
  margin-bottom: 10px; }

.banner-cap-btns-list {
  li {
    margin-bottom: 10px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 15px;
    &:last-child {
      margin-right: 0; } } }

.banner-value-list__value {
  display: block;
  background-image: linear-gradient(60deg, $cl-site-7 0%, #f76894 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: $cl-light;
  font-size: 36px;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.9px;
  margin-bottom: 5px; }

.banner-value-list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  li {
    max-width: 145px;
    min-width: 145px;
    padding-right: 10px;
    > *+* {
      margin-bottom: 20px; } }
  p {
    color: #fdecd4;
    letter-spacing: 0.35px; } }

.banner-cap__tt-bg {
  color: rgba(#978d9f, 0.2);
  font-size: 40px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 1.82px;
  text-align: right; }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .banner-cap {
    &__txt {
      font-size: 20px; } }

  .banner-cap-btns__tt {
    font-size: 16px; }

  .banner-cap-grid {
    display: flex;
    justify-content: space-between;
    &__content {
      min-width: 60%;
      max-width: 60%; }
    &__image {
      min-width: 38%;
      max-width: 38%; } }

  .banner-cap_push-bottom {
    padding-bottom: 180px; }

  .banner-value-list {
    li {
      max-width: 165px;
      min-width: 165px; } }

  .banner-value-list_nowrap {
    flex-wrap: nowrap; }

  .banner-cap__txt-max-width {
    max-width: 620px; }

  .banner-cap__tt-bg {
    font-size: 70px;
    order: 1; }


  .banner-cap {
    .banner-cap__mix-tt {
      .title-site, h1, h2, h3, h4, h5, h6 {
        margin-bottom: 0; } } }
  .banner-cap__mix-tt {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    .title-site, h1, h2, h3, h4, h5, h6 {
      flex-group: 1;
      padding-right: 20px; }
    .banner-cap__tt-bg {
      flex-shrink: 0; } }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .banner-cap .title-site, .banner-cap h1, .banner-cap h2, .banner-cap h3, .banner-cap h4, .banner-cap h5, .banner-cap h6 {
    margin-bottom: 27px; }
  .banner-cap__btn {
    margin-top: 68px;
    margin-bottom: 88px; }
  .banner-cap-btns__tt {
    margin-bottom: 23px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .banner-cap-grid__content {
    padding-top: 65px; }
  .banner-cap_push-bottom {
    padding-top: 21px; }
  .banner-cap_push-bottom {
    padding-bottom: 295px;
    &_md {
      padding-bottom: 100px; }
    &_sm {
      padding-bottom: 50px; }
    &_small {
      padding-bottom: 30px; } }
  .banner-cap__tt-bg {
    font-size: 110px; }
  .banner-cap-grid__image {
    max-width: 39%; }

  .banner-cap-grid_second {
    .banner-cap-grid__image {
      min-width: 43%;
      max-width: 43%; }
    .banner-cap-grid__content {
      min-width: 53%;
      max-width: 53%; } }

  /* end media min width 1200 pixels */ }

