/* ----- Courses page ------------------- */


/* max width 767 pixels */
@media only screen and (max-width: 767px) {
  .title-site_mob-sm {
    font-size: 22px; }
  /* end media max width 767 pixels */ }

.c-img {
  box-shadow: 0 0 20px rgba(14, 17, 25, 0.2);
  border-radius: 10px;
  background-color: $cl-site-second2;
  max-width: 300px;
  height: 250px;
  overflow: hidden;
  margin: 0 auto 24px;
  >img {
    object-fit: cover;
    height: 100%;
    width: 100%; } }

.c-desc {
  text-align: center;
  &__name {
    color: #252525;
    font-size: 14px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0.35px;
    margin-bottom: 15px;
    b {
      font-weight: 700;
      letter-spacing: 0.4px; } } }

.courses-grid__preview {
  margin-bottom: 30px; }

.c-info__td {
  border-bottom: 1px solid darken(#f8f7f8, 15%);
  padding-bottom: 10px;
  margin-bottom: 10px; }

.c-info__td_mob-hidden {
  display: none; }

.c-list-st {
  color: #252525;
  font-size: 12px;
  letter-spacing: 0.35px;
  line-height: 18px;
  b {
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 0.4px; }
  p, li {
    margin-bottom: 10px; } }

.courses-grid {
  margin-bottom: 30px; }

.courses-vector {
  display: none; }

/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .c-desc {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    &__name {
      margin-bottom: 0;
      margin-right: 20px; }
    &__link {
      flex-shrink: 0; } }
  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .c-desc {
    &__name {
      font-size: 14px;
      line-height: 22px;
      b {
        font-size: 16px; } } }

  .c-list-st {
    font-size: 14px;
    line-height: 22px;
    b {
      font-size: 16px; } }

  .courses-grid {
    display: flex;
    justify-content: space-between;
    &__preview {
      min-width: 35%;
      max-width: 35%; }
    &__info {
      min-width: 65%;
      max-width: 65%; } }
  .c-desc {
    display: block;
    &__name {
      margin-right: 0;
      margin-bottom: 15px; } }

  .c-info__td {
    padding-left: 20px;
    margin-left: 20px;
    border-left: 1px solid darken(#f8f7f8, 15%);
    margin-bottom: 0px;
    padding-top: 20px; }

  .courses-grid_wrapp {
    .courses-grid__preview {
      order: 1; }
    .c-info__td {
      border-right: 1px solid #d4ced4;
      border-left: none;
      padding-left: 0;
      &:first-child {
        &:before {
          display: none; } } }
    .c-info {
      padding-left: 0;
      padding-right: 20px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .c-info__td_mob-hidden {
    display: block; }

  .courses-grid {
    &__preview {
      min-width: 30%;
      max-width: 30%; }
    &__info {
      min-width: 70%;
      max-width: 70%; } }

  .c-info {
    display: table;
    padding-left: 20px;

    &__row {
      display: table-row;
      &:nth-child(1) {
        .c-info__td_center {
          vertical-align: middle; } } }
    &__td {
      display: table-cell;
      margin: 0;
      padding: 15px 15px 5px;
      &_center {
        text-align: center; } } }

  .c-list-st {
    >*+* {
      margin-top: 15px; }
    p, li {
      margin-bottom: 5px; } }
  /* end media min width 1000 pixels */ }

/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .courses-grid {
    &__preview {
      min-width: 500px;
      max-width: 500px; }
    &__info {
      min-width: 670px;
      max-width: 670px; } }

  .c-img {
    max-width: 500px;
    height: 370px; }

  .c-desc {
    display: flex;
    justify-content: space-between;
    &__name {
      margin-bottom: 0;
      margin-right: 20px; } }

  .c-info__row {
    &:first-child {
      .c-info__td {
        padding-top: 0; } }
    &:last-child {
      .c-info__td {
        &:before {
          display: none; }
        border-bottom: none; } } }

  .c-info__td {
    .title-site, h1, h2, h3, h4, h5, h6 {
      margin-bottom: 10px; }
    &:last-child {
      width: 165px; }
    &:nth-child(1) {
      position: relative;
      &:before {
        content: ' ';
        position: absolute;
        right: 100%;
        bottom: -1px;
        height: 1px;
        width: 20px;
        background-color: darken(#f8f7f8, 15%); } } }

  .courses-grid {
    margin-bottom: 60px;
    &:last-child {
      margin-bottom: 0; } }

  .courses-grid_wrapp {
    .c-info__td {
      padding: 15px 15px 5px;
      &:first-child {
        padding-left: 0; }
      &:last-child {
        position: relative;
        &:before {
          position: absolute;
          left: 100%;
          content: ' ';
          bottom: -1px;
          height: 1px;
          width: 20px;
          background-color: #d4ced4; } } } }


  .section-st_courses {
    position: relative;
    z-index: 1; }

  .courses-vector {
    display: block; }
  .courses-vector__item {
    position: absolute;
    z-index: -1;
    left: 50%;
    animation: {
      name: courses-vector;
      duration: 10s;
      timing-function: linear;
      iteration-count: infinite;
      direction: alternate-reverse; }

    &_one {
      top: 227px;
      margin-left: -610px; }
    &_two {
      top: 563px;
      margin-left: 500px;
      animation: {
        delay: 1s; } }
    &_three {
      top: 1163px;
      margin-left: 580px;
      animation: {
        delay: 2s; } } }

  @keyframes courses-vector {
    from {
      transform: translateY(0); }
    to {
      transform: translateY(80px); } }
  /* end media min width 1200 pixels */ }

/* ----- END Courses page --------------- */
