/* ----- Index page -------------------- */

.bl-courses {
  box-shadow: 0 0 30px rgba(123, 32, 83, 0.4);
  border-radius: 10px;
  background-color: $cl-site-second2;
  &__cap {
    color: $cl-light;
    font-size: 26px;
    font-weight: 400;
    line-height: 1.2;
    letter-spacing: 0.65px;
    padding: 20px 15px;
    border-bottom: 1px solid #4a3a57;
    text-align: center; }

  &__body {
    li {
      border-bottom: 1px solid #4a3a57; } } }

.my-course {
  display: block;
  text-decoration: none;
  outline: none;
  padding: 10px 15px;
  color: $cl-light;
  text-align: center;
  &:hover, &:focus {
    background-color: $cl-site-6; }

  &__data {
    color: $cl-light;
    font-size: 14px;
    line-height: 1.5;
    letter-spacing: 0.35px;
    margin-bottom: 5px; }
  &__name {
    color: #ffdea8;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 0.45px;
    margin-bottom: 10px; }
  &__persona {
    color: $cl-light;
    font-size: 14px;
    line-height: 1.5;
    letter-spacing: 0.35px;
    margin-bottom: 5px; } }

.bl-courses-push-top-minus {
  padding: 30px 0; }

.bl-courses__footer {
  text-align: center;
  padding: 20px 15px; }

.img-set {
  max-width: 465px;
  margin: 0 auto;

  &__item {
    img {
      box-shadow: 0 0 20px rgba(14, 17, 25, 0.2);
      border-radius: 5px;
      background-color: $cl-site-8;
      overflow: hidden; } }
  &__one {
    position: relative;
    z-index: 2; }
  &__two {
    margin-top: -140px;
    position: relative;
    z-index: 1;
    text-align: right; } }

.tt-big-light {
  color: $cl-light-2;
  font-size: 36px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 2.2px;
  overflow: hidden;
  &_sm {
    font-size: 24px; } }

.txt-bl-grid__content {
  color: #2e2e2e; }

.skills-cards {
  margin-right: -5px;
  margin-left: -5px;
  &__item {
    padding-left: 5px;
    padding-right: 5px;
    margin-bottom: 10px; } }

.skills-card {
  box-shadow: 0 0 20px rgba(14, 17, 25, 0.2);
  border-radius: 5px;
  background-color: $cl-light;
  display: flex;
  justify-content: flex-start;
  overflow: hidden;
  &__icon {
    position: relative;
    z-index: 1;
    background-color: #554165;
    flex-shrink: 0;
    min-width: 100px;
    max-width: 100px;
    min-height: 95px;
    .icon {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      margin: auto; } }

  &__txt {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 15px;
    color: #262626;
    font-size: 12px;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: 0.45px;
    max-width: 270px; } }

.section-st_dark_skills {
  background-image: url(../img/skills-bg.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 30px 0 1px; }

.skills-values {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  &__item {
    padding: 0 5px;
    min-width: 50%;
    max-width: 50%;
    margin-bottom: 20px; } }

.skills-value {
  margin-bottom: 20px;
  text-align: center;
  &__cap {
    background-image: linear-gradient(60deg, $cl-site-7 0%, #f76894 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: $cl-light;
    font-size: 30px;
    font-weight: 700;
    line-height: 1;
    letter-spacing: 1.55px;
    margin-bottom: 10px; }
  &__info {
    color: #fdecd4;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.5px; } }

.my-course__icon {
  margin-bottom: 10px; }
.my-course__txt {
  color: $cl-light;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.3px;
  p {
    margin-bottom: 10px; }
  b {
    color: #ffdea8;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.35px; } }

.my-course__value {
  color: $cl-light;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.35px;
  margin-bottom: 10px; }

a.my-course {
  &:hover {
    border-radius: 10px; } }

.start-bl-btns {
  li {
    margin-bottom: 15px; } }

.start-bl-grid__img {
  max-width: 470px;
  margin: 0 auto 30px;
  position: relative;
  .icon {
    position: absolute;
    &_ic-vector1 {
      top: 20px;
      right: 0; }
    &_ic-vector4 {
      bottom: 0;
      right: 20px; }
    &_ic-vector5 {
      left: 20px;
      bottom: 0; } } }

.section-st_dark_bg-one {
  background-image: url(../img/bg1.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover; }

.card-btn {
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.4);
  border-radius: 10px;
  background-color: $cl-site-second2;
  text-align: center;
  padding: 1px 15px 20px;
  margin: 35px 0; }

.card-btn__btn-push-top-minus {
  margin-top: -27px;
  margin-bottom: 20px; }

.card-btn__txt {
  color: $cl-light;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  letter-spacing: 0.45px;
  margin-bottom: 20px; }

.bl-img_video {
  position: relative;
  img {
    transition-duration: 0.2s; }
  .icon-play {
    position: absolute;
    width: 60px;
    height: 60px;
    top: 50%;
    left: 50%;
    margin-left: -30px;
    margin-top: -30px;
    background-color: rgba(#d1d1d1, 0.7);
    border-radius: 50%;
    transition-duration: 0.2s;
    .icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-40%, -50%); } }

  &:hover {
    img {
      transform: scale(1.1); }
    .icon-play {
      background-color: rgba(#d1d1d1, 0.9); } } }

.bl-img {
  height: 200px;
  display: block;
  text-decoration: none;
  outline: none;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover; } }

.info-personal__name {
  color: #010101;
  font-size: 20px;
  font-weight: 600;
  line-height: 26px;
  letter-spacing: 0.5px;
  margin-bottom: 10px; }

.info-personal__subtt {
  color: #262626;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  letter-spacing: 0.35px;
  margin-bottom: 10px; }

.brand-bl__grid {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  text-align: center;
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: 40px;
  li {
    padding: 20px 10px;
    min-width: 33.3%;
    max-width: 33.3%; } }

.skills-cards_wrapp {
  .skills-card {
    box-shadow: 0 0 20px rgba(14, 17, 25, 0.2);
    border-radius: 5px;
    background-color: $cl-light;
    flex-direction: column;
    overflow: visible;
    margin-bottom: 50px; }

  .skills-card__icon {
    width: 113px;
    height: 87px;
    border-radius: 5px;
    background-color: #554165;
    margin: -30px auto 5px;
    text-align: center;
    line-height: 87px; } }

.skills-card {
  &__body {
    padding: 20px;
    > * + * {
      margin-top: 10px;
      margin-bottom: 0; } }
  &__tt {
    color: #262626;
    font-size: 20px;
    font-weight: 400;
    line-height: 1.2;
    letter-spacing: 0.65px; } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .bl-img {
    height: 300px; }
  /* end media min width 480 pixels */ }

/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .skills-cards {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__item {
      max-width: 300px;
      min-width: 300px; } }

  .skills-values {
    &__item {
      padding: 0 5px;
      min-width: 25%;
      max-width: 25%;
      margin-bottom: 20px; } }

  .card-btns {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
    &__item {
      min-width: 50%;
      max-width: 50%;
      padding-left: 15px;
      padding-right: 15px;
      .card-btn {
        margin-bottom: 10px; } } }
  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .bl-courses-push-top-minus {
    padding-top: 0;
    margin-top: -134px; }
  .bl-courses {
    max-width: 970px;
    margin: 0 auto; }
  .my-course {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    padding: 20px 15px;
    &__link {
      flex-shrink: 0;
      display: block;
      text-align: right; }
    &__data, &__name, &__persona {
      margin-right: 15px;
      margin-bottom: 0; } }
  .bl-courses__footer {
    padding: 30px 15px; }

  .txt-bl-grid {
    display: flex;
    justify-content: space-between;
    &__content {
      max-width: 58%;
      min-width: 58%; }
    &__img {
      min-width: 40%;
      max-width: 40%; } }
  .tt-big-light {
    font-size: 100px;
    &_sm {
      font-size: 60px; }
    &_mini {
      font-size: 54px; } }

  .skills-cards {
    position: relative;
    z-index: 1;
    background: url(../img/logo-content-border.jpg) center center no-repeat;
    background-size: 240px auto; }

  .skills-value {
    &__cap {
      font-size: 40px; }
    &__info {
      font-size: 16px;
      line-height: 22px; } }

  .my-course__icon,
  .my-course__value,
  .my-course__txt {
    margin-bottom: 0;
    margin-right: 15px; }
  .my-course__icon {
    flex-shrink: 0; }
  .my-course__value {
    flex-shrink: 0; }
  .my-course__txt {
    p {
      margin-bottom: 0; } }

  .start-bl-grid {
    display: flex;
    justify-content: space-between;
    &__img {
      order: 1;
      min-width: 40%;
      max-width: 40%;
      margin-right: 0; }
    &__content {
      max-width: 57%;
      min-width: 57%; } }

  .card-btn__txt {
    font-size: 16px;
    line-height: 24px; }

  .bl-img_video {
    .icon-play {
      width: 84px;
      height: 84px;
      margin-left: -42px;
      margin-top: -42px; } }

  .main-grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .main-grid__item {
      min-width: 50%;
      max-width: 50%;
      .info-personal {
        padding-left: 40px; }
      .brand-bl {
        padding-right: 24px; } } }

  .skills-cards.skills-cards_wrapp {
    background-image: url(../img/logo-content-border2.jpg);
    .skills-cards__item {
      margin-bottom: 50px;
      min-width: 290px;
      max-width: 290px;
      .skills-card {
        margin-bottom: 0;
        height: 100%; } } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {

  .skills-value {
    &__cap {
      font-size: 62px; }
    &__info {
      font-size: 20px;
      line-height: 28px; } }
  .my-course {
    padding: 20px 40px; }
  .tt-big-light {
    font-size: 150px;
    &_sm {
      font-size: 100px; }
    &_mini {
      font-size: 78px; } }

  .skills-card {
    &__txt {
      font-size: 18px;
      line-height: 28px; } }

  .skills-cards {
    background-size: auto;
    &__item {
      max-width: 380px;
      min-width: 380px;
      margin-bottom: 9px;
      margin-top: 9px;
      &:nth-child(2n) {
        margin-left: 100px; }
      &:nth-child(2n-1) {
        margin-right: 100px; } }
    &_wrapp {
      .skills-cards__item {
        &:nth-child(2n) {
          margin-left: 150px; }
        &:nth-child(2n-1) {
          margin-right: 150px; }
        &:nth-last-child(-n+2) {
          margin-bottom: 0; } } } }

  .bl-courses_full {
    max-width: none;
    width: 100%; }

  .bl-courses__body_second {
    .my-course {
      padding: 30px; }
    .my-course__icon,
    .my-course__value,
    .my-course__txt {
      margin-right: 30px; } }

  .card-btn__txt {
    font-size: 18px;
    line-height: 32px; }

  .card-btns {
    &__item {
      min-width: 33.333%;
      max-width: 33.333%; } }

  .card-btn__btn-push-top-minus {
    margin-bottom: 30px; }
  .card-btn__txt {
    margin-bottom: 30px; }
  .card-btn {
    padding-bottom: 30px; }
  .main-grid {
    .main-grid__item {
      &_lg {
        min-width: 56%;
        max-width: 56%; }
      &_sm {
        min-width: 44%;
        max-width: 44%; }
      .info-personal {
        padding-left: 50px;
        padding-top: 40px; }
      .form-st {
        margin-left: 20px; } } }

  .bl-img {
    height: 455px; }

  .info-personal__name, .info-personal__subtt {
    margin-bottom: 15px; }

  .start-bl-grid__content {
    padding-top: 20px;
    p {
      color: #2e2e2e;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: 0.4px; } }

  .start-bl-btns {
    margin-top: 40px;
    li {
      margin-bottom: 30px; } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .bl-courses-push-top-minus {
    margin-top: -150px; }

  .bl-courses {
    position: relative;
    &:after {
      content: ' ';
      position: absolute;
      z-index: -1;
      background-image: url(../img/sprite.png);
      background-repeat: no-repeat;
      width: 97px;
      height: 97px;
      background-position: -5px -99px;
      bottom: -31px;
      left: -38px; } }

  .bl-courses_second-icon {
    &:after {
      content: ' ';
      position: absolute;
      z-index: -1;
      background-image: url(../img/sprite.png);
      background-repeat: no-repeat;
      width: 97px;
      height: 97px;
      background-position: -5px -99px;
      top: -31px;
      right: -38px;
      bottom: auto;
      left: auto; }
    &:before {
      content: ' ';
      position: absolute;
      z-index: -1;
      bottom: -42px;
      left: -26px;
      background-image: url(../img/sprite.png);
      background-repeat: no-repeat;
      width: 119px;
      height: 132px;
      background-position: -400px -204px; } }

  .txt-bl-grid_circle-bg {
    position: relative;
    z-index: 1;
    &:after {
      width: 367px;
      height: 367px;
      content: ' ';
      position: absolute;
      top: 50%;
      right: 0;
      margin-top: -150px;
      background: url(../img/img-circle1.png) center center no-repeat;
      background-size: contain;
      animation: linear circle-bg 40s infinite; }
    &_second {
      &:after {
        background-image: url(../img/img-circle2.png); } } }

  @keyframes circle-bg {
    0% {
      transform: rotate(0deg); }
    25% {
      transform: rotate(-45deg); }
    50% {
      transform: rotate(0deg); }
    75% {
      transform: rotate(45deg); }
    100% {
      transform: rotate(0deg); } }

  .img-set__two {
    margin-top: -180px; }

  .start-bl-grid__img {
    .icon_ic-vector1 {
      animation: linear rotate 50s infinite; }
    .icon_ic-vector4 {
      animation: linear rotate2 60s infinite; }

    .icon_ic-vector5 {
      animation: linear vector5 40s infinite; } }

  @keyframes vector5 {
    0% {
      transform: translateY(0); }
    100% {
      transform: translateY(-300%); } }

  @keyframes rotate {
    0% {
      transform: rotate(0); }
    100% {
      transform: rotate(360deg); } }

  @keyframes rotate2 {
    0% {
      transform: rotate(0); }
    100% {
      transform: rotate(-360deg); } }

  .start-bl-grid__content {
    padding-top: 45px; }

  .start-bl-btns {
    margin-top: 50px;
    li {
      margin-bottom: 35px; } }
  /* end media min width 1200 pixels */ }


/* Min width 1350 pixels */
@media only screen and (min-width: 1350px) {
  .txt-bl-grid_circle-bg {
    &:after {
      right: -70px; } }
  /* end media min width 1350 pixels */ }

/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .txt-bl-grid_circle-bg {
    &:after {
      right: -100px; } }
  /* end media min width 1440 pixels */ }

/* Min width 1600 pixels */
@media only screen and (min-width: 1600px) {
  .txt-bl-grid_circle-bg {
    &:after {
      right: -195px; } }
  /* end media min width 1600 pixels */ }

/* ----- END index page /--------------- */
