/* ----- Contacts page ------------------- */
.contact-list-wrapp {
  margin: 40px 0; }
.txt-icon-bl {
  display: inline-block;
  vertical-align: top;
  text-decoration: none;
  outline: none;
  color: #262626;
  font-size: 14px;
  font-weight: 600;
  line-height: 30px;
  letter-spacing: 0.5px;
  position: relative;
  padding-left: 40px;
  margin-bottom: 20px;
  .icon {
    position: absolute;
    top: 5px;
    left: 0;
    &_ic-contact1 {
      left: 2px; }
    &_ic-contact2 {
      top: 7px; }
    &_ic-contact3 {
      left: 4px; } } }

.txt-op {
  color: rgba(#978d9f, 0.2);
  font-size: 38px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 2.08px;
  overflow: hidden; }

.txt-md {
  color: $cl-light;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: 0.5px;
  margin-bottom: 20px; }

.social-list-second {
  li {
    margin-bottom: 25px;
    display: inline-block; }
  >*+* {
    margin-left: 20px; }

  a {
    text-decoration: none;
    outline: none;
    transition: all 0.2s linear;
    &:hover, &:focus {
      opacity: 0.8;
      transform: scale(0.9); } } }

.form-contact {
  input {
    border-radius: 5px;
    border: 2px solid #a9a9a9;
    background-color: transparent;
    @include placeholder-color(#b4b4b4);
    height: 52px; } }
.form-contact__tt {
  color: #fdecd4;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: 0.55px;
  margin-bottom: 10px; }

.txt-light-main {
  color: #c5c5c5;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: 0.65px;
  margin-bottom: 15px;
  &_lg {
    color: #d6d6d6;
    font-size: 20px;
    font-weight: 800;
    line-height: 1.3;
    letter-spacing: 0.75px;
    margin-bottom: 20px; } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .txt-icon-bl {
    font-size: 16px;
    line-height: 32px; }

  .contact-grid-points {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__item {
      width: 49%; } }

  .txt-op {
    font-size: 60px; }

  .txt-md {
    font-size: 20px; }

  .form-contact__tt {
    font-size: 22px; }

  .form-contact__grid {
    display: flex;
    align-items: center;
    .form-group {
      &:nth-child(1) {
        flex-grow: 1;
        margin-right: 28px; }
      &:nth-child(2) {
        flex-shrink: 0; } } }


  .txt-light-main {
    font-size: 26px;
    &_lg {
      font-size: 30px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .contact-list-wrapp {
    margin-top: 100px; }
  .contact-grid-points {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__item {
      width: 32%;
      &_sm {
        width: 28%; }
      &_lg {
        width: 38%; } } }
  .txt-icon-bl {
    font-size: 20px;
    line-height: 36px;
    margin-bottom: 0; }

  .contact-form-box {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    &__left-col {
      width: 36%; }
    &__right-col {
      width: 60%;
      flex-shrink: 0; } }
  /* end media min width 1000 pixels */ }


/* ----- END Contacts page --------------- */
