/* ----- Forms ------------------------- */
.form-st {
  margin-bottom: 20px;

  &__tt {
    display: block; } }

.form-group {
  margin-bottom: 15px;
  &_md {
    margin-bottom: 30px; }
  &_btn {
    margin-top: 40px; }

  &__label {
    display: block;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 18px; }

  &_inline {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .form-group__label {
      min-width: 140px;
      max-width: 140px;
      padding: 0 10px 0 0;
      margin: 5px 0; } } }

.form-group_check {
  display: flex;
  justify-content: flex-start;
  .form-group__label {
    order: 1; } }

.form-st {
  &_dark {
    background-color: $cl-site-second2;
    color: #fff;
    .form-group__field {
      border: 2px solid #a9a9a9;
      background-color: transparent;
      border-radius: 5px;
      color: #fff;
      @include placeholder-color(#b4b4b4); } }
  &__txt {
    text-align: center;
    color: $cl-light;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: 0.45px;
    margin-bottom: 20px;
    &_sm {
      font-weight: 400; } } }

.form-st_order {
  padding: 20px;
  border-radius: 10px; }

.form-st__tt {
  color: $cl-light;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;
  letter-spacing: 0.6px; }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .form-group-grid {
    display: flex;
    justify-content: space-between;

    &__half-col {
      width: 48.5%; }

    &__three-col {
      width: 31.3%; }

    &__four-col {
      width: 22.8%; } }

  .form-st {
    &__txt {
      font-size: 16px;
      line-height: 22px; } }


  .form-st_bg {
    position: relative;
    &:before {
      content: ' ';
      position: absolute;
      width: 85px;
      height: 90px;
      left: -30px;
      bottom: -35px;
      background: url(../img/icons/ic-vector2.png) center center no-repeat;
      background-size: 100% 100%;
      z-index: -1;
      animation: linear form-st_bg 50s infinite; } }

  @keyframes form-st_bg {
    0% {
      transform: rotate(0); }
    100% {
      transform: rotate(360deg); } }

  .form-st__tt {
    font-size: 24px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .form-st {
    &__txt {
      font-size: 18px;
      line-height: 28px;
      &_sm {
        font-size: 14px;
        line-height: 22px; } } }

  .form-st_order {
    padding: 45px 35px 30px; }

  .form-st__txt {
    margin-bottom: 30px; }
  /* end media min width 1000 pixels */ }


/* Min width 1350 pixels */
@media only screen and (min-width: 1350px) {
  .form-st_bg {
    position: relative;
    &:after {
      content: ' ';
      position: absolute;
      width: 81px;
      height: 81px;
      right: -31px;
      top: -31px;
      background: url(../img/icons/ic-vector1.png) center center no-repeat;
      background-size: 100% 100%;
      z-index: -1;
      animation: linear form-st_bg 50s infinite; } }
  /* end media min width 1350 pixels */ }

/* ----- END Forms --------------------- */
